/* 在CSS文件中引入antd的css文件 */
@import '~antd/dist/antd.css';
*{
    padding: 0;
    margin: 0;
}
body{
    background-color: #ccc;
}
.header{
    height: 50px;
    background-color: #444;
}
.header header{
    width: 1200px;
    line-height: 50px;
    margin: 0 auto;
}
.header header a{
    margin: 0 20px;
    color: #fff;
}
.header img{
    width: 120px;
}

/* content */
.content{
    width: 1200px;
    margin: 30px auto 0;
    height: 600px;
}
.left{
  width: 800px;
  height: 30px;
  float: left;
}
.right{
  width: 270px;
  float: right;
  height: 500px;
  margin-right: 50px;
}


.home-title{
    margin: 0 10px;
    background-color: #80bd01;
    color: #fff;
    padding: 2px 4px;
    border-radius: 3px;
}
.home{
    width: 800px;
    float: left;
    height: 30px;
    margin-left: 50px;
    background-color: #f6f6f6;
    border-radius: 3px 3px 0 0;
    padding-top: 4px;
}

.inner{
    width: 800px;
    height: 35px;
    float: left;
    margin-left: 50px;
    border-bottom: 1px solid #f0f0f0;
    background-color: white;
}
.infor{
    width: 800px;
    float: left;
    margin-left: 50px;
    background-color: white;
    padding: 0px;
    
}
.user_avatar img{
    width: 25px;
    height: 25px;
    margin:5px;
}
.reply-count{
    width: 70px;
    display: inline-block;
    text-align: center;
}
.count{
    font-size: 5px;
}
.reply{
    font-size: 15px;
    color: #9e78c0;
}
.pull-right{
    float: right;
    margin-top: 6px;
}
.pull-right img{
    width: 15px;
    height: 15px;
    border-radius: 3px;
}
.pull-right span{
    font-size: 11px;
    color: #778087;
    margin-right: 10px;
    margin-left: 18px;
}
.topic_title_wrapper{
    white-space: nowrap;
}
.put_top{
    background: #80bd01;
    padding: 2px 4px;
    border-radius: 3px;
    color: #fff;
    font-size: 12px;
    margin-left: 15px;
}
.topic_title{
    color: #333;
    max-width: 70%;
    -o-text-overflow: ellipsis;
    white-space: nowrap;
    display: inline-block;
    vertical-align: middle;
    font-size: 10px;
    line-height: 30px;
}

.put_ask{
    background-color: #e5e5e5;
    padding: 2px 4px;
    border-radius: 3px;
    color: #999;
    font-size: 12px;
    margin-left: 15px;
}
.put_share{
    background-color: #e5e5e5;
    padding: 2px 4px;
    border-radius: 3px;
    color: #999;
    font-size: 12px;
    margin-left: 15px;
}

.Share{
    width: 800px;
    margin-left: 50px;
}
.Share-inner{
    width: 800px;
    height: 50px;
    background-color:blue;
}


/* ------------------Right-Adver------------------ */
.Right-Adver{
    width: 270px;
    height: 234px;
    background-color: white;
    float: right;
    padding: 10px;
    border-radius: 0 0 3px 3px;
    margin-bottom: 20px;
}
.adver-a img{
    height: auto;
    max-width: 100%;
    vertical-align: middle;
    border: 0;
}
.sep10{
    height: 10px;
}
/*--------------------------------------------------*/


/* -------------------top----------------------- */
.topic1{
    font-size: 13px;
    margin-bottom: 13px;
    width: 270px;
    float: right;
    margin-bottom: 20px
}
.topic1-header{
    color: #51585c;
    border-radius: 3px 3px 0 0;
    padding: 10px;
    background-color: #f6f6f6;
}
.topic1-inner{
    line-height: 2em;
    padding: 10px;
    border-radius: 0 0 3px 3px;
    background-color: #fff;
}

.topic1-inner ul{
    padding: 0;
    margin: 0 0 10px 25px;
    margin-left: 0;
    list-style: none;
    display: block;
}
/*------------------------------------------------*/

/*--------------right-head------------------------*/
.right-head{
    margin-bottom: 13px;
    width: 270px;
    font-size: 14px;
    float: right;
}
.right-head-inner{
    padding: 10px;
    border-radius: 0 0 3px 3px;
    background-color: #fff;
    line-height: 2em;
}

.right-head-inner p{
    display: block;
    font-weight: bolder;
}
.right-head-span{
    display: inline-block;
    float: none;
    padding: 3px 10px;
    border: none;
    margin: 0;
    font-size: 14px;
    transition: all .2s ease-in-out;
    cursor: pointer;
    letter-spacing: 2px;
    box-shadow: none;
    border-radius: 3px;
    line-height: 2em;
    vertical-align: middle;
    color: #fff;
    background-color: #5bc0de;
}
/*-----------------------------------------------*/
/*----------------pagin-----------------------*/
.pagin{
    display: inline;
    padding-left: 10px;
}
.pagin li{
    line-height: 35px;
    margin-right: 5px;
    color: #999;
    display: inline;
    padding:4px 12px;
    border:1px solid #999;
    border-radius: 4px;
}
.pagin a{
    color: silver;
}
/*--------------------------------------------------*/
/*-------------------------login-header-----------------*/
.login-header{
    padding: 10px;
    background-color: #f6f6f6;
    border-radius: 3px 3px 0 0;
}
.login-header ul{
    padding: 0;
    margin: 0;
    border: none;
    background: 0 0;
    list-style: none;
}
.login-header a{
    text-decoration: none;
    color: #80bd01;
}
.login-header span{
    padding: 0 5px;
    color: #ccc;
}
.login-header li{
    color: #999;
    text-shadow: none;
    display: inline-block;
    list-style: none;
}
.login-inner{
    padding: 10px;
    border-radius: 0 0 3px 3px;
    background-color: #fff;
}
.control-group{
    margin-bottom: 20px;
}
.control-label{
    float: left;
    width: 160px;
    padding-top: 5px;
    text-align: right;
    line-height: 20px;
}
.controls{
    padding-top: 5px;
    margin-left: 180px;
}
.input-xlarge{
    display: inline-block;
    margin-bottom: 0;
    vertical-align: middle;
    border: 1px solid #ccc;
    height: 20px;
    font-size: 14px;
    color: #555;
    border-radius: 4px;
}
.login-submit{
    width: 180px;
    height: 30px;
    border-radius: 10px;
    background-color: #5bc0de;
    margin-bottom: 50px;
    margin-top: 15px;
    margin-left: 140px;
}
.infor ul li{
    list-style-type:none;  
}